body {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.del {
  cursor: pointer;
  text-decoration: underline;
}

.App {
  text-align: center;
}

div {
  box-sizing: border-box;
}

.like-btn {
  cursor: pointer;
}

.toast {
  position: absolute;
  top: 100px;
  left: 0;
  right: 0;
  margin: 0 auto;
  display: flex;
  gap: 16px;
  background-color: #111;
  opacity: 0.8;
  color: #fff;
  width: fit-content;
  height: fit-content;
  padding: 16px 24px;
  border-radius: 8px;
  box-shadow: 0 16px 24px rgba(0, 0, 0, 0.3);
}
h4,
button {
  cursor: pointer;
}

.list {
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid grey;
}

.modal {
  margin-top: 20px;
  padding: 20px;
  background-color: #eee;
  text-align: left;
}

.black-nav {
  display: flex;
  background-color: black;
  width: 100%;
  color: white;
  padding-left: 20px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
